body {
  margin: 0;
  padding: 5em 3em;
  box-sizing: border-box;
  background: #E8E4E1;
  height: 100%;
  width: 100%;
  font-family: 'Nunito Sans', sans-serif;
}

.button {
  text-decoration: none;
  color: black;
  text-transform: uppercase;
}

.button-primary {
  margin-top: 5em;
  padding: 0.5em 1.5em;
  font-size: 12px;
  border: 1px solid #1F5369;
  color: #1F5369;
  border-radius: 10px;
}

.page-wrapper {
  position: absolute;
  display: flex;
  padding: 4em 3em;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 1.5em;
  max-width: 68em;
  width: 100%;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  max-height: 34em;
  height: 100%;
  box-shadow: 75px 114px 84px 55px rgba(0, 0, 0, 0.15);
}

@media (max-width: 1200px) {
  .page-wrapper {
    max-height: 54em;
    max-width: 48em;
  }
}

.header-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 22em;
  width: 100%;
}

.main-header {
  display: flex;
}

.header-logo-text {
  font-size: 18px;
  padding-right: .5em;
  margin: auto;
}

.nav-list {
  margin: auto;
}

.list-item {
  display: inline-block;
  list-style: none;
  margin-right: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 10px;
  color: rgba(0, 0, 0, 0.8);
}

.landing-container {
  max-width: 11em;
  width: 100%;
}

.landing-title {
  font-size: 34px;
  font-weight: 400;
  margin-bottom: 0;
}

.landing-text {
  font-size: 12px;
  font-weight: bold;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.3);
  margin-bottom: 4em;
}

.shape-container {
  height: 34em;
  height: 100%;
  max-width: 50em;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.shape-row-one,
.shape-row-two {
  display: flex;
}

@media (max-width: 1200px) {
  .shape-row-one,
  .shape-row-two {
    flex-wrap: wrap;
  }
}

.one-container,
.two-container,
.three-container,
.four-container {
  display: flex;
  flex-direction: row;
  max-width: 23.25em;
}

@media (max-width: 1200px) {
  .three-container {
    overflow: hidden;
  }
}

.shape-row-one div,
.shape-row-two div {
  max-width: 23.25em;
  width: 100%;
  height: 11.25em;
}

.shape-row-one-first {
  background-color: #1F5369;
  border-top-left-radius: 11em;
}

.shape-row-one-second {
  background-color: #87CDD5;
  border-bottom-right-radius: 11em;
}

.shape-row-circle {
  border-radius: 11em;
  background-color: #E7DBC1;
}

.shape-row-circle:last-of-type {
  background-color: #1F5369;
}

.shape-row-one-fourth {
  border-bottom-left-radius: 11em;
  background-color: #F8BCBC;
}

.shape-row-two-first-pink {
  border-top-left-radius: 11em;
  background-color: #F8BCBC;
}

.shape-row-two .shape-row-two-second {
  height: 22em;
  border-top-left-radius: 11em;
  border-bottom-left-radius: 11em;
  background-color: #FF7C67;
}

.shape-row-two-third-blue {
  border-bottom-left-radius: 11em;
  background-color: #87CDD5;
}

.shape-row-two-fourth-green {
  border-top-left-radius: 11em;
  background-color: #28543D;
}

.shape-row-two-fourth-beige {
  border-bottom-left-radius: 11em;
  background-color: #E7DBC1;
}
